body {
  font-family: Helvetica, arial, sans-serif;
}

.AdsForm {
  width: 680px;
  margin: 50px auto;
}

.AdsForm__header {
  font-size: 24px;
  letter-spacing: 0.06px;
  padding-bottom: 5px;
  margin-top: 30px;
}

.AdsForm__description {
  margin: 0px 0px 18px;
  color: rgba(26, 43, 77, 0.6);
  font-size: 14px;
}

.AdsForm__description-warn {
  color: red;
  animation: warnblink 1s ease-in-out;
}

@keyframes warnblink {
  from, to, 50% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}

.AdsForm__label {
  display: block;
}

.AdsForm__subdescription {
  color: rgba(26, 43, 77, 0.6);
  font-size: 12px;
}

.AdsForm__required {
  color: red;
  font-size: 15px;
  vertical-align: top;
}

.AdsForm__line {
  display: flex;
  align-items: baseline;
  margin-bottom: 25px;
}

.AdsForm__sitelinks-headers {
  margin-bottom: 0;
}

.AdsForm__sitelinks-header {
  width: calc(100%/3);
  margin-left: 10px;
}

.AdsForm__sitelinks-header:first-child {
  margin-left: 0px;
}

.AdsForm__input {
  flex: 0 1 auto;
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  border: 1px solid rgba(134, 148, 176, 0.6);
  border-radius: 5px;
  padding: 4px 10px;
  margin: 5px 10px 0px 0px;
  font-size: 14px;
}

.AdsForm__input:focus {
  outline: none;
  border: 2px solid rgb(0, 29, 158);
  padding: 3px 9px;
}

.AdsForm__input-warn {
  background-color: #ffd6d6;
}

.AdsForm__input-sitelinks {
  margin-left: 10px;
  padding-right: 24px;
}

.AdsForm__input-sitelinks:first-child {
  margin-left: 0px;
}

.AdsForm__input-sitelinks:focus {
  padding-right: 23px;
}

.AdsForm__info {
  width: 20px;
  color: rgba(26, 43, 77, 0.6);
}

.AdsForm__info-warn {
  color: red;
}

.AdsForm__info-sitelinks {
  flex-shrink: 0;
  margin-left: -30px;
  font-size: 13px;
}

.AdsForm__extsitelinks-show {
  color: rgb(73, 139, 255);
  border-bottom: 1px dashed rgb(73, 139, 255);
  cursor: pointer;
  display: inline-block;
  margin-bottom: 10px;
}

.AdsForm__extsitelinks-show:hover {
  color: rgb(242, 13, 13);
  border-color: rgb(242, 13, 13);
}